import {useState, useEffect} from 'react';
import { useParams } from 'react-router-dom';
import PropTypes from 'prop-types';
import useMarvelService from '../../services/MarvelService';
import setContent from '../../utils/setContent';
import AppBanner from '../appBanner/AppBanner';

const SingleComicPage = ({Component, dataType}) => {
    const {id} = useParams();
    const [data, setData] = useState(null);
    const {getComics, getCharacters, clearError, process, setProcess} = useMarvelService();
    
    useEffect(() => {
        updateData();
        //eslint-disable-next-line
    }, [id])

    const updateData = () => {
        setProcess('loading');
        clearError();
        switch (dataType) {
            case 'comics':
                getComics(id).then(onDataLoaded).then(() => setProcess('confirmed'));
                break;
            case 'char':
                getCharacters(id).then(onDataLoaded).then(() => setProcess('confirmed'));
                break;
            default:
                throw new Error('Unexpected error');
        }
    }

    const onDataLoaded = (data) => {
        setData(data);
    }

    return (
        <>
            <AppBanner/>
            {setContent(process, Component, data)}
        </>
    )
}

export default SingleComicPage;